/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.alinear-izquierda {
  display: flex;
  justify-content: flex-start;
}

.w-100 {
  width: 100%;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode td {
  color: #FFFFFF;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode label {
  color: #e1e1e1;
}
.dark-mode .anuncio {
  background-color: #333333;
}
.dark-mode .iconos-anuncio {
  filter: invert(100%);
}
.dark-mode .iconos-anuncio p {
  color: #000000;
}
.dark-mode .formulario p {
  color: #FFFFFF;
}
.dark-mode .formulario legend {
  color: #FFFFFF;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  color: #FFFFFF;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  color: #FFFFFF;
  padding: 3rem;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  font-weight: bold;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  background-color: #333333;
  text-align: center;
  margin-bottom: 0;
}
.footer a {
  color: #FFFFFF;
  text-decoration: none;
  padding: 1rem;
}
.footer a:hover {
  color: #71B100;
}
.footer nav {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .footer nav {
    text-align: left;
    flex-direction: row;
  }
}

.footer-contenedor {
  padding: 2.5rem 0 1rem 0;
}

.copyright {
  color: #FFFFFF;
}

.derecha {
  align-items: center;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.dark-mode-boton {
  width: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.dark-mode-boton:hover {
  cursor: pointer;
}

.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem;
}
@media (min-width: 768px) {
  .navbar {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  height: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 1s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    visibility: visible;
    opacity: 1;
  }
}
.navegacion a {
  display: flex;
  flex-direction: column;
  color: #FFFFFF;
  text-decoration: none;
  margin-inline: 1rem;
  text-align: center;
  margin-top: 2rem;
}
.navegacion a:hover {
  color: #71B100;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 1s linear;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono {
  text-align: center;
}
.icono img {
  height: 15rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  margin-bottom: 2rem;
  border: solid 1px #333333;
  background-color: #e1e1e1;
}
@media (min-width: 768px) {
  .anuncio {
    margin-bottom: 0;
  }
}

.info-anuncio {
  margin: 1rem;
}
.info-anuncio span {
  color: #71B100;
  font-weight: bold;
}

.iconos-anuncio {
  list-style: none;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(6, 1fr);
  padding: 1rem 2rem;
  max-width: 40rem;
}
.iconos-anuncio img {
  height: 3rem;
  align-content: center;
}
.iconos-anuncio p {
  margin-top: 0;
  font-weight: bold;
}

.btnAnuncio {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  border: none;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  cursor: pointer;
  margin: 1rem;
  display: block;
  margin-bottom: 2rem;
}
.btnAnuncio:hover {
  background-color: #c77808;
}

.btnVerde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: bold;
  border: none;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  cursor: pointer;
  margin: 1rem;
  display: flex;
  justify-content: center;
  margin-top: 3rem;
}
.btnVerde:hover {
  background-color: #619800;
}

.btnContactanos {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  border: none;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  cursor: pointer;
  margin: 1rem;
  display: inline-block;
  text-align: center;
}
.btnContactanos:hover {
  background-color: #c77808;
}

.btnRojo-block {
  background-color: rgb(168, 8, 8);
  color: #FFFFFF;
  font-weight: bold;
  border: none;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  cursor: pointer;
  margin: 1rem;
  display: block;
  text-align: center;
}
.btnRojo-block:hover {
  background-color: #900707;
}

.btnAmarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  border: none;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  cursor: pointer;
  margin: 1rem;
  display: block;
  text-align: center;
}
.btnAmarillo-block:hover {
  background-color: #c77808;
}

.no-webp .contactanos {
  background-image: url("../img/encuentra.jpg");
}

.webp .contactanos {
  background-image: url("../img/encuentra.jpg");
}

.contactanos {
  background-position: center center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem 0;
}

.contactanos-contenido {
  margin-top: 3rem;
}
.contactanos-contenido h2 {
  color: #FFFFFF;
  font-weight: bold;
}
.contactanos-contenido p {
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 768px) {
  .blog {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 3rem;
  }
}
.blog h3 {
  font-weight: 400;
}

.nuestro-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .nuestro-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.nuestro-blog:last-of-type {
  margin-bottom: 0;
}

.blog-info a {
  color: #000000;
  margin-top: 5rem;
  display: block;
}
@media (min-width: 768px) {
  .blog-info a {
    margin-top: 0;
  }
}
.blog-info h4 {
  margin: 0;
  line-height: 1.4;
}
.blog-info h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.blog-info span {
  color: #E08709;
}

.testimonial {
  background-color: #71B100;
  border-radius: 5%;
}

.testimonial-info {
  display: grid;
  grid-template-columns: 1fr 4fr;
  color: #FFFFFF;
  padding-top: 3rem;
}
.testimonial-info img {
  padding-left: 3rem;
}
.testimonial-info blockquote {
  margin-left: 1rem;
}
.testimonial-info p {
  color: #FFFFFF;
  text-align: right;
}

.seccion h1 {
  font-weight: 500;
}

@media (min-width: 768px) {
  .sobre-nosotros {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .sobre-nosotros h4 {
    margin-top: 0;
  }
}

.info-nosotros h4 {
  font-weight: bold;
}
.info-nosotros p {
  text-align: justify;
}

.formulario {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
}
.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  margin-left: 2rem;
  font-weight: bold;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.alerta {
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  font-weight: bold;
  margin: 1rem 0;
  border-radius: 1rem;
  border: 1px solid #000000;
}
.alerta.error {
  background-color: rgb(168, 8, 8);
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades td {
  text-align: center;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}

.imagen-small {
  width: 20rem;
}