.no-webp .contactanos{
    background-image: url('../img/encuentra.jpg');
}
.webp .contactanos{
    background-image: url('../img/encuentra.jpg');
}
.contactanos{
    background-position: center center;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3rem 0;
}
.contactanos-contenido{
    h2{
        color: $blanco;
        font-weight: bold;
    }
    p{
        color: $blanco;
        text-align: center;
    }
    margin-top: 3rem;
}